import React , {  useState } from 'react'
import './login.css'
import {Link,useHistory} from 'react-router-dom'
import {auth} from './firebase'


function Login() {
    const history = useHistory()

    const [email,setEmail] = useState('')
    const [password,setPassword] = useState('')

    const signIn = e =>{
        e.preventDefault()    
        
        auth.signInWithEmailAndPassword(email,password)
        .then(auth=>{
            history.push('/')
        }).catch(error=>alert(error.message))
        
        
                                                                //prevent refresing page
    }

    const register = e =>{
        e.preventDefault();

        auth 
            .createUserWithEmailAndPassword(email,password).then((auth)=>{
                console.log(auth)
                if(auth){
                    history.push('/')
                }

            })
            .catch(error=>alert(error.message))

        

    }




    return (
        <div className='login' >
            <Link to='/'>
            <img  className='loginLogo' src='data:image/png;base64,' />
            </Link>
            <div className='loginContainer'>
                <h1>Sign In</h1>
                <form>
                    <h5>E-mail</h5>
                    <input type='text'  value={email} onChange={e =>setEmail(e.target.value)}/>
                    <h5>Password</h5>
                    <input type='password'  value={password} onChange={e=> setPassword(e.target.value)}  />

                    <button  type ='submit'className='loginSigninButton' onClick={signIn}>
                        Sign In</button>
                </form>
                <p>
                    By signing-in you agree to Amazon's 
                    Conditions of Use and Sale.Please
                    see our Privacy Notice,our Cookies Notice
                    and out Interest based Ads
                </p>

                <button onClick={register} className='loginRegisterButton'>Create your Account</button>

            </div>
            
        </div>
    )
}

export default Login
